@import url(https://fonts.googleapis.com/css?family=Open+Sans|Roboto&display=swap);
@import url(https://fonts.googleapis.com/css?family=Volkhov|Vollkorn&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

body,#app{overflow:hidden;font-family:'Roboto', sans-serif}body #carouselContainer,#app #carouselContainer{overflow:hidden}body h1,body h2,body h3,body h4,body h5,#app h1,#app h2,#app h3,#app h4,#app h5{font-family:'Roboto', sans-serif;font-weight:bold}body #pageIndicator,#app #pageIndicator{background-color:rgba(255,255,255,0.5)}body #navigator button,#app #navigator button{color:rgba(0,0,0,0.5);background-color:rgba(255,255,255,0.25);border:none}body #navigator button:hover,#app #navigator button:hover{color:rgba(0,0,0,0.85);background-color:rgba(255,255,255,0.5)}body #navigator button:disabled,#app #navigator button:disabled{color:rgba(0,0,0,0.25);background-color:rgba(255,255,255,0.125)}body #thumbnailHeader button,#app #thumbnailHeader button{color:#000;background-color:#fff;border:none;font-size:1.5rem}body #thumbnailHeader button:hover,#app #thumbnailHeader button:hover{color:#fff;background-color:rgba(0,0,0,0.2)}body p,body ul,body li,#app p,#app ul,#app li{font-family:'Vollkorn', serif;font-size:1.75rem}body #halaman1 p,#app #halaman1 p{font-size:1.75rem}body #halaman4 p,#app #halaman4 p{font-size:1.5rem;text-indent:3rem}body .carousel-caption,#app .carousel-caption{top:0;bottom:auto;background-color:rgba(0,0,0,0.5)}body #thumbnailContainer,#app #thumbnailContainer{overflow:auto}body #thumbnailHeader,#app #thumbnailHeader{font-family:'Roboto', sans-serif}body .thumbnail-img,#app .thumbnail-img{-webkit-transition:all .5s;transition:all .5s;opacity:.6}body .thumbnail-wrapper,#app .thumbnail-wrapper{overflow:hidden}body .thumbnail-wrapper .thumbnail-text span,#app .thumbnail-wrapper .thumbnail-text span{font-family:'Vollkorn', serif;font-size:1.75rem}body .thumbnail-wrapper:hover .thumbnail-img,body .thumbnail-wrapper:active .thumbnail-img,body .thumbnail-wrapper:focus .thumbnail-img,body .thumbnail-wrapper:disabled .thumbnail-img,#app .thumbnail-wrapper:hover .thumbnail-img,#app .thumbnail-wrapper:active .thumbnail-img,#app .thumbnail-wrapper:focus .thumbnail-img,#app .thumbnail-wrapper:disabled .thumbnail-img{-webkit-transform:scale(1.125);transform:scale(1.125);opacity:1}body .thumbnail-wrapper .thumbnail-text,#app .thumbnail-wrapper .thumbnail-text{display:none}body .thumbnail-wrapper:hover .thumbnail-text,body .thumbnail-wrapper:active .thumbnail-text,body .thumbnail-wrapper:focus .thumbnail-text,body .thumbnail-wrapper:disabled .thumbnail-text,#app .thumbnail-wrapper:hover .thumbnail-text,#app .thumbnail-wrapper:active .thumbnail-text,#app .thumbnail-wrapper:focus .thumbnail-text,#app .thumbnail-wrapper:disabled .thumbnail-text{display:flex}

